import Image from "next/image";
import styles from "./image.module.css";
type Props = {
  url: string;
};
export const Card = ({ url }: Props) => {
  return (
    <div className={styles.imageContainer}>
      <Image
        alt="cat"
        src={url}
        width={225}
        height={225}
        unoptimized
        priority
      />
      <button>
        <Image alt="heart" src="/Vector.svg" width={40} height={36.7} />
      </button>
    </div>
  );
};
